/* tslint:disable */
import { NgModule, ModuleWithProviders } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationInterface } from './api-configuration';

import { ApplicationService } from './services/application.service';
import { MenuRestControllerService } from './services/menu-rest-controller.service';
import { CommentRestControllerService } from './services/comment-rest-controller.service';
import { DishRestControllerService } from './services/dish-rest-controller.service';
import { BasicErrorControllerService } from './services/basic-error-controller.service';
import { UserRestControllerService } from './services/user-rest-controller.service';

/**
 * Provider for all Api services, plus ApiConfiguration
 */
@NgModule({
  imports: [
    HttpClientModule
  ],
  exports: [
    HttpClientModule
  ],
  declarations: [],
  providers: [
    ApiConfiguration,
    ApplicationService,
    MenuRestControllerService,
    CommentRestControllerService,
    DishRestControllerService,
    BasicErrorControllerService,
    UserRestControllerService
  ],
})
export class ApiModule {
  static forRoot(customParams: ApiConfigurationInterface): ModuleWithProviders {
    return {
      ngModule: ApiModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: {rootUrl: customParams.rootUrl}
        }
      ]
    }
  }
}
